<template>
  <div class="ex2">
    <div class="app-sec">
      <div class="app-col">
        <h2>Example 2: With Default Scrollbar</h2>
        <p>Content scroll in horizontal dimension with <code>scrollLeft</code> property.</p>
        <pre data-highlight>
let viewport = document.querySelector('.ex2 .tbl-main')
let content = viewport.querySelector('table')

let sb = new ScrollBooster({
  mode: 'x',
  emulateScroll: false,
  viewport,
  content,
  onUpdate: (data)=> {
    viewport.scrollLeft = data.position.x
  }
})</pre>
      </div>
      <div class="app-col">
        <app-table></app-table>
      </div>
    </div>
  </div>
</template>

<script>
import ScrollBooster from 'scrollbooster'
import AppTable from './Table.vue'

export default {
  name: "app-example2",
  components: { AppTable },
  data() {
    return {}
  },
  mounted() {
    let viewport = document.querySelector('.ex2 .tbl-main')
    let content = viewport.querySelector('table')

    let sb = new ScrollBooster({
      mode: 'x',
      emulateScroll: false,
      viewport,
      content,
      onUpdate: (data)=> {
        viewport.scrollLeft = data.position.x
      }
    })
  }
}

</script>

<style lang="less">
.ex2 .tbl {
  background: #fff;
  &-main {
    overflow-x: scroll;
  }
}
</style>



